.footer {
    background-color: $cor-header;
    

        .rodape {
            display: flex;
            flex-wrap: wrap;
            gap: 2rem;
            justify-content: center;
            align-items: center;
            padding: 3.5rem 0;
            
        }

        .logo__rodape {
            width: 300px;
            margin: 0 auto;
        }

        .rodape__info {
            max-width: 520px;
        }

        .rodape p {
            @include paragrafo;
        }

        .redes__sociais {
            display: flex;
            gap: 0.5rem;
            justify-content: center;
            align-items: center;
        }

        .rede__img:hover {
            box-shadow: 2px 2px 10px 2px #000;
            transition: all .6s;
        }

    .rodape__endereco {
        padding: 1rem;
    }    

    .endereco__titulo {
        font-family: $fonte-titulo;
        font-size: 2rem;
        text-align: center;
        margin-bottom: 1rem;

            
    }

    .endereco__info {
        background-color: #234C66;
        padding: 1rem;
        border-radius: 10px;
        box-shadow: 2px 2px 10px 2px #000;
    }

    .rodape__rua,
    .rodape__bairro,
    .rodape__cidade {
        display: flex;
        gap: .5rem;
        align-items: center;
        font-family: $fonte-corpo;
        font-size: 1.5rem;
        color: #fff;

            @include celular {
                font-size: 1rem;
                padding-bottom: 1rem;
            }
        
    }

    .rodape__horarios {
        margin-top: 2rem;

    }

    .rodape__func {
        font-family: $fonte-corpo;
        font-size: 1.5rem;
        text-align: center;
        background-color: #37769c;
        box-shadow: 2px 2px 10px 2px #000;
        padding: 0.8rem;
        border-radius: 10px;
    }

    .funcionamento {
        font-family: $fonte-titulo;
        font-size: 2rem;
        text-align: center;
        margin-bottom: 1rem;
    }

    .hora__semana,
    .hora__almoco {
        display: flex;
        gap: .5rem;
        align-items: center;
        justify-content: center;
        font-family: $fonte-corpo;
        font-size: 1.5rem;
        color: #fff;

            @include celular {
                font-size: 1rem;
                padding-bottom: 0.4rem;
            }
    }

    .desenvolvedor {
        padding: 0.8rem;
        background-color: #000;
        text-align: center;
    }

    .img__dev {
        margin: 0 auto;
    }

    .dev__dev p {
        color: #fff;
        font-family: $fonte-titulo;
    }

    .dev__link {
        max-width: 300px;
        margin: 0 auto;
        padding: .4rem;
    }

    .dev__link:hover {
        background-color: $cor-header;
        border-radius: 10px;
        transition: all .6s;
    }
}